<template>
    <div>
      <b-card title="Reseas de Productos" sub-title="">
        <b-card-text v-if="loading">Cargando reseas...</b-card-text>
        <b-card-text v-else-if="error">{{ error }}</b-card-text>
  
        <div v-else>
          <b-card-group>
            <b-card v-for="resea in reseas" :key="resea.Id_Resea">
              <template #header>
                <strong>Producto ID: {{ resea.Id_Producto }}</strong>
              </template>
  
              <b-card-body>
                <p><strong>Calificacin:</strong> {{ resea.calificacion }} </p>
                <p><strong>Comentario:</strong> {{ resea.comentario }}</p>
                <p><strong>Fecha:</strong> {{ resea.fecha }}</p>
              </b-card-body>
            </b-card>
          </b-card-group>
        </div>
      </b-card>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    name: 'ReseasView',
    data() {
      return {
        reseas: [],
        loading: true,
        error: null,
      };
    },
    mounted() {
      this.fetchReseas();
    },
    methods: {
      async fetchReseas() {
        try {
          this.loading = true;
          this.error = null;
          const token = localStorage.getItem('token');
          console.log('jwt token:', token);
          console.log(`Llamando a la URL: /api/resenhas`);
          const response = await axios.get(`/api/resenhas`, {
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${token}`,
            },
          });
          console.log('Datos recibidos:', response.data);
          this.reseas = response.data.content; 
        } catch (error) {
          console.log(error);
          this.handleError(error, 'No se pudieron cargar las reseas');
        } finally {
          this.loading = false;
        }
      },
      handleError(error, mensajePredeterminado) {
        console.error('Error:', error);
        this.error = error.response?.data?.message || mensajePredeterminado;
      }
    }
  };
  </script>
  
  <style scoped>
  .reseas-container {
    padding: 20px;
  }
  </style>
  